import url from "~/mixins/url.js";

export default {
  mixins: [url],
  props: {
    post: {
      required: true,
      type: Object,
    },
  },
  data() {
    return {
      collapseStatus: false,
      showPosts: false,
    };
  },
  methods: {
    convertDate(date) {
      const newDate = new Date(date);
      const monthNames = [
        "Ocak",
        "Şubat",
        "Mart",
        "Nisan",
        "Mayıs",
        "Haziran",
        "Temmuz",
        "Ağustos",
        "Eylül",
        "Ekim",
        "Kasım",
        "Aralık",
      ];
      const day = newDate.getDate();
      const monthIndex = newDate.getMonth();
      const year = newDate.getFullYear();
      return `${day} ${monthNames[monthIndex]} ${year}`;
    },
  },
  mounted() {
    setTimeout(() => {
      this.showPosts = true;
    }, 1000);
  },
};
